<template>
	<section>
		<!---->
		<el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
			<el-form :inline="true" :model="filters">
				<el-form-item>
					<el-input v-model="filters.name" placeholder=""></el-input>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" v-on:click="getUsers"></el-button>
				</el-form-item>
				<el-form-item>
					<el-button type="success" @click="startAward"></el-button>
				</el-form-item>
				<el-form-item>
					<el-button type="warning" @click="pauseAward"></el-button>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" @click="unpauseAward"></el-button>
				</el-form-item>
				<el-form-item>
					<el-button type="danger" @click="endAward"></el-button>
				</el-form-item>
			</el-form>
		</el-col>

		<!---->
		<el-table :data="instances" highlight-current-row v-loading="listLoading" @selection-change="selsChange" style="min-width: 100%;">
			<el-table-column type="selection" width="55">
			</el-table-column>
			<el-table-column type="index" width="60">
			</el-table-column>
			<el-table-column prop="name" label="" min-width="180" sortable>
			</el-table-column>
			<!--<el-table-column prop="sex" label="" width="100" :formatter="formatSex" sortable>
			</el-table-column>-->
			<el-table-column prop="teamname" label="" min-width="180" sortable>
			</el-table-column>
			<el-table-column prop="chalname" label="chal" min-width="180" sortable>
			</el-table-column>
			<el-table-column prop="ip" label="ip" min-width="180" sortable>
			</el-table-column>
			<el-table-column prop="password" label="ssh" min-width="180" sortable>
			</el-table-column>
			<el-table-column prop="status" label="" min-width="100" sortable>
			</el-table-column>
			<!--<el-table-column prop="birth" label="" width="120" sortable>
			</el-table-column>-->
			<!--
			<el-table-column prop="addr" label="" min-width="180" sortable>
			</el-table-column>-->
			<el-table-column label="" min-width="200">
				<template scope="scope">
					<el-button size="small" @click="handleRestart(scope.$index, scope.row)"></el-button>
					<el-button type="warning" size="small" @click="handleEdit(scope.$index, scope.row)">ssh</el-button>
				</template>
			</el-table-column>
		</el-table>

		<!---->
		<el-col :span="24" class="toolbar">
			<el-button type="danger" @click="batchRemove" :disabled="this.sels.length===0"></el-button>
			<el-pagination layout="prev, pager, next" @current-change="handleCurrentChange" :page-size="20" :total="total" style="float:right;">
			</el-pagination>
		</el-col>

		<!---->
		<el-dialog title="" v-model="editFormVisible" :close-on-click-modal="false">
			<el-form :model="editForm" label-width="80px" :rules="editFormRules" ref="editForm">

				<el-form-item label="">
					<el-input type="password" v-model="editForm.addr"></el-input>
				</el-form-item>
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click.native="editFormVisible = false"></el-button>
				<el-button type="primary" @click.native="editSubmit" :loading="editLoading"></el-button>
			</div>
		</el-dialog>

		<!---->
		<el-dialog title="" v-model="addFormVisible" :close-on-click-modal="false">
			<el-form :model="addForm" label-width="80px" :rules="addFormRules" ref="addForm">
				<el-form-item label="" prop="name">
					<el-input v-model="addForm.name" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="">
					<el-radio-group v-model="addForm.sex">
						<el-radio class="radio" :label="1"></el-radio>
						<el-radio class="radio" :label="0"></el-radio>
					</el-radio-group>
				</el-form-item>
				<el-form-item label="">
					<el-input-number v-model="addForm.age" :min="0" :max="200"></el-input-number>
				</el-form-item>
				<el-form-item label="">
					<el-date-picker type="date" placeholder="" v-model="addForm.birth"></el-date-picker>
				</el-form-item>
				<el-form-item label="">
					<el-input type="textarea" v-model="addForm.addr"></el-input>
				</el-form-item>
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click.native="addFormVisible = false"></el-button>
				<el-button type="primary" @click.native="addSubmit" :loading="addLoading"></el-button>
			</div>
		</el-dialog>
	</section>
</template>

<script>
	import util from '../../common/js/util'
	//import NProgress from 'nprogress'
	import { getInstancesListPage, removeUser, batchRemoveUser, editInstance, addUser, restartInstance, confirmMessageCallback ,startAll, pauseAll, unpauseAll, endAll} from '../../api/api';

	export default {
		data() {
			return {
				filters: {
					name: ''
				},
				instances: [],
				total: 0,
				page: 1,
				listLoading: false,
				sels: [],//

				editFormVisible: false,//
				editLoading: false,
				editFormRules: {
					name: [
						{ required: true, message: '', trigger: 'blur' }
					]
				},
				//
				editForm: {
					id: 0,
					password:''
				},

				addFormVisible: false,//
				addLoading: false,
				addFormRules: {
					name: [
						{ required: true, message: '', trigger: 'blur' }
					]
				},
				//
				addForm: {
					name: '',
					sex: -1,
					age: 0,
					birth: '',
					addr: ''
				}

			}
		},
		methods: {
			//
			formatSex: function (row, column) {
				return row.sex == 1 ? '' : row.sex == 0 ? '' : '';
			},
			handleCurrentChange(val) {
				this.page = val;
				this.getUsers();
			},
			//
			getUsers() {
				let para = {
					page: this.page,
					name: this.filters.name
				};
				this.listLoading = true;
				//NProgress.start();
				getInstancesListPage(para).then((res) => {
					this.total = res.data.total;
					this.instances = res.data.instances;
					this.listLoading = false;
					//NProgress.done();
				});
			},
			//
			handleDel: function (index, row) {
				this.$confirm('?', '', {
					type: 'warning'
				}).then(() => {
					this.listLoading = true;
					//NProgress.start();
					let para = { id: row.id };
					removeUser(para).then((res) => {
						this.listLoading = false;
						//NProgress.done();
						this.$message({
							message: '',
							type: 'success'
						});
						this.getUsers();
					});
				}).catch(() => {

				});
			},
			querySuccess:function(id,succMess,failMess){
			    console.log(succMess,failMess)
                let query={'id':id};
                confirmMessageCallback(query).then((res)=>{
                    if(res.data.msg){
                        if(res.data.status=='success') {
                            this.$message({
                                'message': succMess,
                                'type': 'success'
                            });
                            return new Promise((resolve,reject)=>{
                                resolve(res);
							})
                        }
                        else{
                            this.$message.error(failMess+''+res.data.status);
                            return new Promise((resolve,reject)=>{
                                resolve(res);
							})
						}
                    }
                    else{
                        setTimeout(()=> {
                            return this.querySuccess(id, succMess, failMess);
                        },500);
					}
                })
			},
			//
			handleRestart: function (index, row) {
				/**/
				this.$confirm('','',{}).then(()=>{
                   let para={'id':row.id};
                   restartInstance(para).then((res)=>{
                       console.log(res);
                       this.$message({
						   'message':res.data.msg,
						   'type':'success'
					   });
                       setTimeout(()=> {
                           this.querySuccess(res.data.id, '', '');
                       },500);
				   })
				})
			},
			handleEdit:function(index,row){
                this.editFormVisible = true;
                this.editForm = Object.assign({}, row);
			},
			//
			startAward: function () {
				this.$confirm('?','',{}).then(()=>{
				    let para={};
				    startAll(para).then((res)=>{
				        console.log((res));
				        this.$message({
							'message':res.data.msg,
							'type':'success'
						});
				        this.querySuccess(res.data.id,'','').then((res)=>{
				            this.getUsers();
						})
					})
				})
			},
			pauseAward:function(){
                this.$confirm('?','',{}).then(()=>{
                    let para={};
                    pauseAll(para).then((res)=>{
                        console.log((res));
                        this.$message({
                            'message':res.data.msg,
                            'type':'success'
                        });
                        this.querySuccess(res.data.id,'','').then((res)=>{
                            this.getUsers();
                        })
                    })
                })
			},
			unpauseAward:function(){
                this.$confirm('?','',{}).then(()=>{
                    let para={};
                    unpauseAll(para).then((res)=>{
                        console.log((res));
                        this.$message({
                            'message':res.data.msg,
                            'type':'success'
                        });
                        this.querySuccess(res.data.id,'','').then((res)=>{
                            this.getUsers();
                        })
                    })
                })
			},
			endAward:function(){
                this.$confirm('?','',{}).then(()=>{
                    let para={};
                    endAll(para).then((res)=>{
                        console.log((res));
                        this.$message({
                            'message':res.data.msg,
                            'type':'success'
                        });
                        this.querySuccess(res.data.id,'','').then((res)=>{
                            this.getUsers();
                        })
                    })
                })
			},
			//
			editSubmit: function () {
				this.$refs.editForm.validate((valid) => {
					if (valid) {
						this.$confirm('', '', {}).then(() => {
							this.editLoading = true;
							//NProgress.start();
							let para = Object.assign({}, this.editForm);
							para.birth = (!para.birth || para.birth == '') ? '' : util.formatDate.format(new Date(para.birth), 'yyyy-MM-dd');
							editInstance(para).then((res) => {
							    if(res.data.code==200) {
                                    this.editLoading = false;
                                    //NProgress.done();
                                    this.$message({
                                        message: res.data.msg,
                                        type: 'success'
                                    });
                                    this.$refs['editForm'].resetFields();
                                    this.editFormVisible = false;
                                    this.getUsers();
                                    this.querySuccess(res.data.id, '', '');
                                }
                                else{
							        this.$message.error(res.data.msg);
								}
							});
						});
					}
				});
			},
			//
			addSubmit: function () {
				this.$refs.addForm.validate((valid) => {
					if (valid) {
						this.$confirm('', '', {}).then(() => {
							this.addLoading = true;
							//NProgress.start();
							let para = Object.assign({}, this.addForm);
							para.birth = (!para.birth || para.birth == '') ? '' : util.formatDate.format(new Date(para.birth), 'yyyy-MM-dd');
							addUser(para).then((res) => {
							    if(res.data.code==200) {
                                    this.addLoading = false;
                                    //NProgress.done();
                                    this.$message({
                                        message: '',
                                        type: 'success'
                                    });

                                    this.$refs['addForm'].resetFields();
                                    this.addFormVisible = false;
                                    this.getUsers();
                                }
                                else{
							        this.$message.error(res.data.msg);
								}
							});
						});
					}
				});
			},
			selsChange: function (sels) {
				this.sels = sels;
			},
			//
			batchRemove: function () {
				var ids = this.sels.map(item => item.id).toString();
				this.$confirm('', '', {
					type: 'warning'
				}).then(() => {
					this.listLoading = true;
					//NProgress.start();
					let para = { ids: ids };
					batchRemoveUser(para).then((res) => {
						if(res.data.code==200) {
                            this.listLoading = false;
                            //NProgress.done();

                            this.$message({
                                message: '',
                                type: 'success'
                            });
                            this.getUsers();
                        }
                        else{
						    $this.$message.error(res.data.msg);
						}

					});
				}).catch(() => {

				});
			}
		},
		mounted() {
			this.getUsers();
		}
	}

</script>

<style scoped>

</style>


